	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Gimp Update","date":"29 Jan 2003","slug":"/2003-01-29-gimp-update/","tags":"Y2003"},"html":"<p>Ten days after her injury, Mama is now able to hobble across the room without crutches. She's able to handle Christopher in the morning, so I've gone back to my normal work schedule. She's back at work, staying in the office. We imagine that next week she'll be able to drive again. </p>\n<p>All in all she's been a very good patient. I've only had to remind her a couple of times to take it easy. She's gotten good treatment from her next-door neighbor (a physical therapist) and her sister-in-law (a <em>Heilpraktikerin</em>, or nonmedical practitioner). She's only seen her doctor once.</p>"},"next":{"fields":{"title":"On This Day: Challenger","date":"28 Jan 2003","slug":"/2003-01-28-on-this-day-challenger/"}},"previous":{"fields":{"title":"American Idols","date":"30 Jan 2003","slug":"/2003-01-30-american-idols/"}}},{"node":{"fields":{"title":"On This Day: Challenger","date":"28 Jan 2003","slug":"/2003-01-28-on-this-day-challenger/","tags":"Y2003"},"html":"<p>BBC On This Day: <a href=\"http://news.bbc.co.uk/onthisday/hi/dates/stories/january/28/newsid_2506000/2506161.stm\">1986: Seven dead in space shuttle disaster</a>. I still know exactly where I was (a bitter-cold parking lot at the U of M in St. Paul) when I heard the news, and I never regained my taste for space travel or science fiction. Thanks to <a title=\"blog.hebig.org\" href=\"http://www.hebig.org/blogs/archives/main/000805.php\">hebig.org</a>  for the reminder.</p>"},"next":{"fields":{"title":"An Axe for the Toolbox","date":"28 Jan 2003","slug":"/2003-01-28-an-axe-for-the-toolbox/"}},"previous":{"fields":{"title":"Gimp Update","date":"29 Jan 2003","slug":"/2003-01-29-gimp-update/"}}},{"node":{"fields":{"title":"An Axe for the Toolbox","date":"28 Jan 2003","slug":"/2003-01-28-an-axe-for-the-toolbox/","tags":"Y2003"},"html":"<p>I'm unlucky enough to have to maintain internal web servers on a few outdated Linux boxes, with various versions of Red Hat, Suse, and Mandrake. Trying to keep Apache and PHP current on them is a nightmare, since current RPM packages are not available for old distributions. (MSSQL admins learned over the weekend how important it is to <a href=\"http://www.theregister.co.uk/content/56/29027.html\">keep your servers  current</a>.) And I simply do not like the way Mandrake packages Apache (with separate servers with and without mod_perl).</p>\n<p>Building my own RPMs would be silly, since I only have one of each box, so I would still have to compile 5 different Apaches. I have been using <a title=\"ApacheToolbox\" href=\"http://www.apachetoolbox.com/\">ApacheToolbox</a> to try to keep things somewhat organized. But compiling a current PHP on an older box with ApacheToolbox is still a nightmare, since 1) it installs PHP as a static module, so updating PHP means updating everything, and 2) PHP gets confused about which libs (system or ApacheToolbox) to link. Given that my boxes are busy with other tasks, trial and error with PHP/Apache compiles takes literally hours.</p>\n<p>Today I happened upon a solution. Compile ApacheToolbox <em>without</em> PHP, then compile PHP as a dynamic module with apxs. ApacheToolbox without PHP compiled and installed cleanly on all boxes, and since PHP only looked for system libraries, it was easy to sort them out. I was able to update all my boxes with various PHP extensions before noon.</p>\n<p>Doing this under FreeBSD is much easier, thanks to the <a href=\"http://www.freshports.org/\">ports</a>.</p>"},"next":{"fields":{"title":"Old European Stereotypes","date":"26 Jan 2003","slug":"/2003-01-26-old-european-stereotypes/"}},"previous":{"fields":{"title":"On This Day: Challenger","date":"28 Jan 2003","slug":"/2003-01-28-on-this-day-challenger/"}}}],"pathPrefix":"","first":false,"last":false,"index":792,"pageCount":1101}}

